	vertical-align: baseline;
	font-weight: inherit;
	font-family: inherit;
	font-style: inherit;
	font-size: 100%;
	border: 0 none;
	outline: 0;
	padding: 0;
	margin: 0;
}

a {
    color: black;
    text-decoration: none;
}

body {
    height: 100vh;
    width: 100vw;
}

.header-container {
    background-color: rgba(0, 0, 255, .9);
    height: 15%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

#header-text {
    font-size: 3rem;
}

.main-container {
    height: 77%;
    width: 100%;
    background-color: rgba(0, 0, 0, .8);
    display: grid;
    grid-template-columns: 5% 95%;
    grid-template-rows: 10% 5% 5% 60% 1.7% 10%;
}

.turn-announcement-container {
    grid-area: 1 / 1 / 2 / 3;
    text-align: center;
    font-weight: bold;
}

.turn-announcement-container h2 {
    font-size: 2rem;
}

.board-info-container {
    display: flex;
    grid-area: 2 / 1 / 3 / 3;
    align-items: end;
    justify-content: center;
    font-weight: bold;
}

.grid-container {
    align-self: center;
    display: flex;
    flex-direction: column;
    height: 80%;
    width: 100%;
    align-items: center;
    justify-content: center;
    grid-area: 4 / 2 / 5 / 3;
}

.column-labels-container {
    font-weight: bold;
    display: grid;
    grid-template-columns: repeat(10, 2rem);
    grid-area: 3 / 2 / 4 / 3;
    align-items: end;
    justify-content: center;
    text-align: center;
    gap: .1rem;
}

.row-labels-container {
    align-self: center;
    font-weight: bold;
    display: grid;
    grid-area: 4 / 1 / 5 / 2;
    align-items: center;
    justify-content: end;
    text-align: center;
    grid-template-rows: repeat(10, 2rem);
    gap: .15rem;
}

.grid {
    display: grid;
    height: min-content;
    width: min-content;
    grid-template-columns: repeat(10, 1fr);
    grid-template-rows: auto;
    gap: 0;
    align-items: center;
    justify-content: center;
}

.cell {
    height: 2rem;
    width: 2rem;
    border: black solid 1px;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Battleship {
    position: relative;
    left: 3.3rem;
    width: 8rem;
}

.Carrier {
    position: relative;
    left: 4.3rem;
    width: 10rem;
}

.Cruiser {
    position: relative;
    width: 6rem;
}

.Destroyer {
    width: 4rem;
    position: relative;
    left: 1rem;
}

.Submarine {
    z-index: 1;
    position: relative;
    left: 2rem;
    width: 6rem;
}

.vertical .Battleship {
    left: 0;
    top: 3.3rem;
}

.vertical .Carrier {
    left: 0;
    top: 4.4rem;
}

.vertical .Cruiser {
    left: 0;
    top: 2.2rem;
}

.vertical .Destroyer {
    left: 0;
    top: 1.1rem;
}

.vertical .Submarine {
    left: 0;
    top: 2.2rem;
}

.cell:hover {
    border: darkblue solid 1px;
}

.error-message {
    display: flex;
    grid-area: 5 / 1 / 6 / 3;
    color: red;
    justify-content: center;
    font-size: .85rem;
}

.sunk-ships-container {
    grid-area: 6 / 1 / 7 / 3;
    display: flex;
    flex-direction: column;
    width: 40%;
}

.sunk-ships-container li {
    font-size: .8rem;
}

.sunk-ships-container h4 {
    font-size: 1.1rem;
}

.player-sunk-ships-container ul {
    list-style-type: none;
    height: 70%;
    display: flex;
    flex-direction: column;
    /* flex-wrap: wrap; */
}

.footer-container {
    background-color: rgba(0, 0, 255, .9);
    height: 8%;
    display: flex;
    align-items: flex-end;
    justify-content: flex-end;
    padding-right: .5%;
}

.credit-container {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: flex-end;
    padding-bottom: .3%;
}

span.is-hit {
    height: 1rem;
    width: 1rem;
    background-color: red;
    border-radius: 50%;
    display: inline-block;
}

.cell.has-ship.is-hit span {
    display: none;
}

.explosion {
    position: absolute;
    height: 2rem;
    width: 2rem;
    z-index: 1;
}

.vertical img {
    transform: rotate(270deg);
}

.winner-container {
    height: 100vh;
    width: 100vw;
    background-color: darkblue;
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 0;
    font-size: 3rem;
    color: palegoldenrod;
}

.hidden {
    display: none;
}`, "",{"version":3,"sources":["webpack://./src/style.css"],"names":[],"mappings":"AAAA;CACC,wBAAwB;CACxB,oBAAoB;CACpB,oBAAoB;CACpB,mBAAmB;CACnB,eAAe;CACf,cAAc;CACd,UAAU;CACV,UAAU;CACV,SAAS;AACV;;AAEA;IACI,YAAY;IACZ,qBAAqB;AACzB;;AAEA;IACI,aAAa;IACb,YAAY;AAChB;;AAEA;IACI,qCAAqC;IACrC,WAAW;IACX,WAAW;IACX,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,WAAW;IACX,mCAAmC;IACnC,aAAa;IACb,6BAA6B;IAC7B,0CAA0C;AAC9C;;AAEA;IACI,wBAAwB;IACxB,kBAAkB;IAClB,iBAAiB;AACrB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,wBAAwB;IACxB,gBAAgB;IAChB,uBAAuB;IACvB,iBAAiB;AACrB;;AAEA;IACI,kBAAkB;IAClB,aAAa;IACb,sBAAsB;IACtB,WAAW;IACX,WAAW;IACX,mBAAmB;IACnB,uBAAuB;IACvB,wBAAwB;AAC5B;;AAEA;IACI,iBAAiB;IACjB,aAAa;IACb,uCAAuC;IACvC,wBAAwB;IACxB,gBAAgB;IAChB,uBAAuB;IACvB,kBAAkB;IAClB,UAAU;AACd;;AAEA;IACI,kBAAkB;IAClB,iBAAiB;IACjB,aAAa;IACb,wBAAwB;IACxB,mBAAmB;IACnB,oBAAoB;IACpB,kBAAkB;IAClB,oCAAoC;IACpC,WAAW;AACf;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,sCAAsC;IACtC,wBAAwB;IACxB,MAAM;IACN,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,uBAAuB;IACvB,aAAa;IACb,mBAAmB;IACnB,uBAAuB;AAC3B;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,WAAW;AACf;;AAEA;IACI,kBAAkB;IAClB,YAAY;IACZ,YAAY;AAChB;;AAEA;IACI,kBAAkB;IAClB,WAAW;AACf;;AAEA;IACI,WAAW;IACX,kBAAkB;IAClB,UAAU;AACd;;AAEA;IACI,UAAU;IACV,kBAAkB;IAClB,UAAU;IACV,WAAW;AACf;;AAEA;IACI,OAAO;IACP,WAAW;AACf;;AAEA;IACI,OAAO;IACP,WAAW;AACf;;AAEA;IACI,OAAO;IACP,WAAW;AACf;;AAEA;IACI,OAAO;IACP,WAAW;AACf;;AAEA;IACI,OAAO;IACP,WAAW;AACf;;AAEA;IACI,0BAA0B;AAC9B;;AAEA;IACI,aAAa;IACb,wBAAwB;IACxB,UAAU;IACV,uBAAuB;IACvB,iBAAiB;AACrB;;AAEA;IACI,wBAAwB;IACxB,aAAa;IACb,sBAAsB;IACtB,UAAU;AACd;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,iBAAiB;AACrB;;AAEA;IACI,qBAAqB;IACrB,WAAW;IACX,aAAa;IACb,sBAAsB;IACtB,qBAAqB;AACzB;;AAEA;IACI,qCAAqC;IACrC,UAAU;IACV,aAAa;IACb,qBAAqB;IACrB,yBAAyB;IACzB,kBAAkB;AACtB;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,qBAAqB;IACrB,yBAAyB;IACzB,mBAAmB;AACvB;;AAEA;IACI,YAAY;IACZ,WAAW;IACX,qBAAqB;IACrB,kBAAkB;IAClB,qBAAqB;AACzB;;AAEA;IACI,aAAa